"use client"

import type React from "react"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Textarea } from "@/components/ui/textarea"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import { Home, Gift, User, LogOut, Menu, ArrowLeft, Settings, Shield } from "lucide-react"
import Link from "next/link"
import { auth, db } from "@/app/firebase"
import { onAuthStateChanged } from "firebase/auth"
import { doc, getDoc, updateDoc } from "firebase/firestore"
import { getStorage, ref as storageRef, uploadBytes, getDownloadURL } from "firebase/storage"
import { updatePassword, EmailAuthProvider, reauthenticateWithCredential } from "firebase/auth"
import { isAuthenticated } from "@/lib/auth"

const storage = getStorage();
const MAX_AVATAR_SIZE = 2 * 1024 * 1024;

export default function EditProfile() {
  const router = useRouter()
  const [sidebarOpen, setSidebarOpen] = useState(false)
  const [user, setUser] = useState<{ displayName: string | null, email: string | null }>({ displayName: null, email: null })
  const [isEditMode, setIsEditMode] = useState(false)
  const [isAdmin, setIsAdmin] = useState<boolean | null>(null)
  const [errorMsg, setErrorMsg] = useState<string | null>(null)

  useEffect(() => {
    if (typeof window !== "undefined" && !isAuthenticated()) {
      router.push("/login");
    }
  }, [])

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (firebaseUser) => {
      if (firebaseUser) {
        setUser({
          displayName: firebaseUser.displayName || "No Username",
          email: firebaseUser.email || "No Email"
        })
        const userDoc = await getDoc(doc(db, "users", firebaseUser.uid))
        if (userDoc.exists()) {
          const data = userDoc.data()
          setProfileData({
            fullName: data.name || "",
            email: data.email || "",
            phone: data.phone || "",
            address: data.address || "",
            city: data.city || "",
            state: data.state || "",
            postalCode: data.postalCode || "",
            bio: data.bio || "",
            avatar: data.avatar || "",
          })
          setIsAdmin(!!data.isAdmin)
        } else {
          setIsAdmin(false)
        }
      } else {
        setIsAdmin(false)
      }
    })
    return () => unsubscribe()
  }, [])

  const [profileData, setProfileData] = useState({
    fullName: "",
    email: "",
    phone: "",
    address: "",
    city: "",
    state: "",
    postalCode: "",
    bio: "",
    avatar: "",
  })
  const [isSubmitting, setIsSubmitting] = useState(false)

  const [avatarFile, setAvatarFile] = useState<File | null>(null);
  const [uploading, setUploading] = useState(false);
  const [uploadProgress, setUploadProgress] = useState<number | null>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setProfileData((prev) => ({
      ...prev,
      [name]: value,
    }))
  }

  // Direct upload to Cloudinary
  const handleAvatarChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (!file) return;
    setAvatarFile(file);
    setUploading(true);
    setUploadProgress(0);
    setErrorMsg(null);
    try {
      const formData = new FormData();
      formData.append('file', file);
      formData.append('upload_preset', 'avatar_upload'); // Your unsigned upload preset
      formData.append('folder', 'avatars');
      const xhr = new XMLHttpRequest();
      xhr.open('POST', 'https://api.cloudinary.com/v1_1/drig5ndvt/image/upload');
      xhr.upload.onprogress = (event) => {
        if (event.lengthComputable) {
          setUploadProgress(Math.round((event.loaded / event.total) * 100));
        }
      };
      xhr.onload = async () => {
        setUploading(false);
        if (xhr.status === 200) {
          const response = JSON.parse(xhr.responseText);
          const url = response.secure_url;
          setProfileData((prev) => ({ ...prev, avatar: url }));
          try {
            const currentUser = auth.currentUser;
            if (!currentUser) throw new Error("No authenticated user");
            const userRef = doc(db, "users", currentUser.uid);
            await updateDoc(userRef, { avatar: url });
            setErrorMsg(null);
          } catch (err: any) {
            setErrorMsg("Failed to update avatar in database.");
          }
        } else {
          setErrorMsg('Upload failed. Please try again.');
        }
      };
      xhr.onerror = () => {
        setUploading(false);
        setErrorMsg('Upload failed. Please try again.');
      };
      xhr.send(formData);
    } catch (err) {
      setUploading(false);
      setErrorMsg('Upload failed. Please try again.');
    }
  };

  const handleResetAvatar = async () => {
    setProfileData((prev) => ({ ...prev, avatar: "" }));
    try {
      const currentUser = auth.currentUser;
      if (!currentUser) throw new Error("No authenticated user");
      const userRef = doc(db, "users", currentUser.uid);
      await updateDoc(userRef, { avatar: "" });
      setErrorMsg(null);
    } catch (err: any) {
      setErrorMsg("Failed to reset avatar in database.");
    }
  };

  const handleEditOrSubmit = async (e: React.FormEvent) => {
    if (!isEditMode) {
      e.preventDefault();
      setIsEditMode(true);
      return;
    }
    e.preventDefault();
    setIsSubmitting(true);
    setErrorMsg(null);
    try {
      const currentUser = auth.currentUser;
      if (!currentUser) throw new Error("No authenticated user");
      let avatarUrl = profileData.avatar;
      const userRef = doc(db, "users", currentUser.uid);
      console.log("Updating Firestore profile...");
      await updateDoc(userRef, {
        name: profileData.fullName,
        email: profileData.email,
        phone: profileData.phone,
        address: profileData.address,
        city: profileData.city,
        state: profileData.state,
        postalCode: profileData.postalCode,
        bio: profileData.bio,
        avatar: avatarUrl,
      });
      setProfileData((prev) => ({ ...prev, avatar: avatarUrl }));
      setIsEditMode(false);
      console.log("Profile updated successfully.");
    } catch (error: any) {
      console.error("Error updating profile:", error);
      setErrorMsg(error?.message || "Failed to update profile. Please try again.");
    } finally {
      setIsSubmitting(false);
    }
  }

  const [showPasswordFields, setShowPasswordFields] = useState(false)
  const [currentPassword, setCurrentPassword] = useState("")
  const [newPassword, setNewPassword] = useState("")
  const [passwordError, setPasswordError] = useState<string | null>(null)
  const [passwordSuccess, setPasswordSuccess] = useState<string | null>(null)
  const [isPasswordSubmitting, setIsPasswordSubmitting] = useState(false)

  const passwordValidation = (password: string) => {
    // At least 6 characters, 1 capital letter, 1 number
    return /^(?=.*[A-Z])(?=.*\d)[A-Za-z\d]{6,}$/.test(password);
  };

  const handlePasswordChange = async (e: React.FormEvent) => {
    e.preventDefault();
    setPasswordError(null);
    setPasswordSuccess(null);
    setIsPasswordSubmitting(true);
    if (!passwordValidation(newPassword)) {
      setPasswordError("Password must be at least 6 characters, include 1 capital letter and 1 number.");
      setIsPasswordSubmitting(false);
      return;
    }
    try {
      const currentUser = auth.currentUser;
      if (!currentUser || !currentUser.email) throw new Error("No authenticated user");
      // Re-authenticate
      const credential = EmailAuthProvider.credential(currentUser.email, currentPassword);
      await reauthenticateWithCredential(currentUser, credential);
      await updatePassword(currentUser, newPassword);
      setPasswordSuccess("Password updated successfully.");
      setShowPasswordFields(false);
      setCurrentPassword("");
      setNewPassword("");
    } catch (error: any) {
      if (error.code === "auth/invalid-credential" || error.message?.includes("auth/invalid-credential")) {
        setPasswordError("The current password you entered is incorrect.");
      } else {
        setPasswordError(error.message || "Failed to update password.");
      }
    } finally {
      setIsPasswordSubmitting(false);
    }
  }

  // Add sign out handler
  const handleSignOut = async () => {
    if (typeof window !== "undefined") {
      localStorage.removeItem("auth");
    }
    await auth.signOut();
    router.push("/login");
  };

  return (
    <>
      <div className="min-h-screen bg-gray-100 flex">
        <div className="lg:hidden fixed top-4 left-4 z-50">
          <Button variant="outline" size="icon" onClick={() => setSidebarOpen(!sidebarOpen)} className="bg-white">
            <Menu className="h-5 w-5" />
          </Button>
        </div>

        <div
          className={`
          fixed inset-y-0 left-0 z-40 w-64 bg-white shadow-lg transform transition-transform duration-300 ease-in-out
          ${sidebarOpen ? "translate-x-0" : "-translate-x-full"} lg:translate-x-0
        `}
        >
          <div className="flex flex-col h-full">
            <div className="p-6 border-b">
              <h2 className="text-2xl font-bold text-green-600">FWRP</h2>
            </div>

            <div className="flex-1 py-6 px-4 space-y-6">
              <div className="flex flex-col items-center mb-8">
                <Avatar className="w-24 h-24 mb-2">
                  <AvatarImage src={profileData.avatar || "/placeholder.svg?height=80&width=80"} alt="Profile" />
                  <AvatarFallback>
                    <User className="h-10 w-10" />
                  </AvatarFallback>
                </Avatar>
                <span className="text-lg font-semibold">
                  {profileData.fullName || user.displayName}
                </span>
              </div>

              <nav className="mt-8 space-y-2">
                {isAdmin === null ? null : (
                  <>
                    <Link href="/edit-profile" className="flex items-center p-3 bg-gray-100 text-green-600 rounded-md">
                      <Settings className="h-5 w-5 mr-3" />
                      Show Profile
                    </Link>
                    {isAdmin ? (
                      <Link href="/admin" className="flex items-center p-3 text-gray-700 rounded-md hover:bg-gray-100">
                        <Shield className="h-5 w-5 mr-3" />
                        Admin Dashboard
                      </Link>
                    ) : (
                      <>
                        <Link href="/dashboard" className="flex items-center p-3 text-gray-700 rounded-md hover:bg-gray-100">
                          <Home className="h-5 w-5 mr-3" />
                          Dashboard
                        </Link>
                        <Link href="/donate-food" className="flex items-center p-3 text-gray-700 rounded-md hover:bg-gray-100">
                          <Gift className="h-5 w-5 mr-3" />
                          Donate Food
                        </Link>
                      </>
                    )}
                  </>
                )}
              </nav>
            </div>

            <div className="p-4 border-t">
              <Button variant="outline" className="w-full justify-start" onClick={handleSignOut}>
                <LogOut className="h-5 w-5 mr-3" />
                Sign out
              </Button>
            </div>
          </div>
        </div>
        {/* Main content */}
        <div className="flex-1 lg:ml-64">
          <header className="bg-white shadow">
            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-4 flex items-center">
              <Link href="/dashboard" className="mr-4">
                <Button variant="ghost" size="sm">
                  <ArrowLeft className="h-5 w-5 mr-2" />
                  Back to Dashboard
                </Button>
              </Link>
              <h1 className="text-3xl font-bold text-gray-900">Show Profile</h1>
            </div>
          </header>
          <main className="max-w-3xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
            <div className="bg-white shadow rounded-lg p-6">
              <h2 className="text-xl font-semibold mb-6">Personal Information</h2>

              <form onSubmit={handleEditOrSubmit} className="space-y-6">
                <div>
                  <Label htmlFor="avatar">Profile Picture</Label>
                  <div className="mt-1 flex items-center space-x-5">
                    <Avatar className="h-20 w-20">
                      <AvatarImage src={profileData.avatar || "/placeholder.svg?height=80&width=80"} alt="Profile" />
                      <AvatarFallback>
                        <User className="h-10 w-10" />
                      </AvatarFallback>
                    </Avatar>
                    {isEditMode && (
                      <>
                        <input
                          type="file"
                          accept="image/*"
                          onChange={handleAvatarChange}
                          className="ml-4"
                          disabled={uploading}
                        />
                        {uploading && (
                          <span className="ml-2 text-blue-600">Uploading... {uploadProgress !== null ? `${uploadProgress}%` : ''}</span>
                        )}
                        <Button type="button" onClick={handleResetAvatar} className="ml-2 bg-gray-200 text-gray-800">Reset Avatar</Button>
                      </>
                    )}
                  </div>
                </div>

                <div className="grid grid-cols-1 gap-6 sm:grid-cols-2">
                  <div>
                    <Label htmlFor="fullName">Full Name</Label>
                    <Input id="fullName" name="fullName" value={profileData.fullName} onChange={handleChange} required readOnly={!isEditMode} />
                  </div>

                  <div>
                    <Label htmlFor="email">Email Address</Label>
                    <Input
                      id="email"
                      name="email"
                      type="email"
                      value={profileData.email}
                      onChange={handleChange}
                      required
                      readOnly={!isEditMode}
                    />
                  </div>

                  <div>
                    <Label htmlFor="phone">Phone Number</Label>
                    <Input id="phone" name="phone" value={profileData.phone} onChange={handleChange} required readOnly={!isEditMode} />
                  </div>

                  <div>
                    <Label htmlFor="address">Address</Label>
                    <Input id="address" name="address" value={profileData.address} onChange={handleChange} required readOnly={!isEditMode} />
                  </div>

                  <div>
                    <Label htmlFor="city">City</Label>
                    <Input id="city" name="city" value={profileData.city} onChange={handleChange} required readOnly={!isEditMode} />
                  </div>

                  <div>
                    <Label htmlFor="state">State/Province</Label>
                    <Input id="state" name="state" value={profileData.state} onChange={handleChange} required readOnly={!isEditMode} />
                  </div>

                  <div>
                    <Label htmlFor="postalCode">Postal Code</Label>
                    <Input
                      id="postalCode"
                      name="postalCode"
                      value={profileData.postalCode}
                      onChange={handleChange}
                      required
                      readOnly={!isEditMode}
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="bio">Bio</Label>
                  <Textarea
                    id="bio"
                    name="bio"
                    value={profileData.bio}
                    onChange={handleChange}
                    placeholder="Tell us a bit about yourself"
                    rows={4}
                    readOnly={!isEditMode}
                  />
                </div>

                {errorMsg && (
                  <div className="mb-4 text-red-600 font-semibold">{errorMsg}</div>
                )}

                <div className="pt-4 flex flex-col gap-2">
                  <Button type="button" variant="outline" onClick={() => setShowPasswordFields((v) => !v)}>
                    {showPasswordFields ? "Cancel Password Change" : "Change Password"}
                  </Button>
                  {showPasswordFields && (
                    <div className="space-y-4 mt-2">
                      <div>
                        <Label htmlFor="currentPassword">Current Password</Label>
                        <Input
                          id="currentPassword"
                          name="currentPassword"
                          type="password"
                          value={currentPassword}
                          onChange={e => setCurrentPassword(e.target.value)}
                          required
                        />
                      </div>
                      <div>
                        <Label htmlFor="newPassword">New Password</Label>
                        <Input
                          id="newPassword"
                          name="newPassword"
                          type="password"
                          value={newPassword}
                          onChange={e => setNewPassword(e.target.value)}
                          required
                          minLength={6}
                        />
                      </div>
                      {passwordError && <div className="text-red-600">{passwordError}</div>}
                      {passwordSuccess && <div className="text-green-600">{passwordSuccess}</div>}
                      <Button onClick={handlePasswordChange} disabled={isPasswordSubmitting} type="button">
                        {isPasswordSubmitting ? "Updating..." : "Update Password"}
                      </Button>
                    </div>
                  )}
                </div>

                <div className="pt-4 flex justify-end space-x-4">
                  {isEditMode && (
                    <Button type="button" variant="outline" onClick={() => setIsEditMode(false)}>
                      Cancel
                    </Button>
                  )}
                  <Button
                    type="submit"
                    disabled={isSubmitting}
                  >
                    {isSubmitting ? "Saving..." : isEditMode ? "Save Changes" : "Edit"}
                  </Button>
                </div>
              </form>
            </div>
          </main>
        </div>


        {sidebarOpen && (
          <div className="fixed inset-0 bg-black bg-opacity-50 z-30 lg:hidden" onClick={() => setSidebarOpen(false)} />
        )}
      </div>
    </>
  )
}
